<template>
	<div class="login">
		<span class="back" @click="back">返回</span>
		<img class="logo" src="../assets/images/logo.png"/>
		<div class="container">
			<div class="login-box">
				<input type="text" placeholder="用户名/手机号/邮箱" v-model="username"/>
				<img src="../assets/images/login1.png" alt="" />
			</div>
			<div class="login-box">
				<input type="password" placeholder="密码" v-model="password"/>
				<img src="../assets/images/login2.png" alt="" />
			</div>
			<button @click="toLogin">立即登录</button>
			<div class="other">
				<span>注册账户</span>
				<span class="forget">忘记密码</span>
			</div>
		</div>
		<div class="auth">
			<img src="../assets/images/qq.png" alt=""/>
			<div class="line"></div>
			<img src="../assets/images/weixin.png" alt=""/>
		</div>
		<div class="error" v-show="msg">{{msg}}</div>
	</div>
</template>
<script>
	export default {
		data(){
			return{
				username: "",
				password: "",
				msg: "",
				timer: null
			}
		},
		methods: {
			back(){
				history.back();
			},
			toLogin(){
				var _self = this;
				if( this.username.trim() == "" ){
					this.showMsg("请输入账号!")
					return;
				}else if( !/^\w{6,12}$/.test(this.username) ){
					this.showMsg("账号格式不正确!");
					return;
				}else if( this.password.trim() == "" ){
					this.showMsg("请输入密码!")
					return;					
				}else if( !/^\w{6,12}$/.test(this.password) ){
					this.showMsg("密码格式不正确!");
					return;
				}
				if( this.username=="youwenchao" && this.password=="123456" ){
					this.showMsg("登录成功！");
					localStorage.setItem("ywc_app", this.username);
					setTimeout(function(){
						_self.$router.push("/person");
					},1500)
				}else{
					this.showMsg("账号或密码错误！");
				}
			},
			showMsg(str){
				clearTimeout(this.timer);
				this.timer = null;
				this.msg = str;
				var _self = this;
				this.timer = setTimeout(function(){
					_self.msg = "";
					clearTimeout(_self.timer);
					_self.timer = null;
				}, 1500)
			}	
		}
	}
</script>
<style lang="scss" scoped>
	@import "../assets/css/_mixin.scss";
	.login {
		width: 100%;
		height: 100%;
		background: #fff;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 10;
	}
	.back {
		@include font-dpr(13px);
		color: #ccc;
		position: absolute;
		@include px2rem(left, 30px);
		@include px2rem(top, 40px);
	}
	.logo {
		@include px2rem(width, 132px);
		@include px2rem(height, 128px);
		display: block;
		margin: 20% auto 0;
	}
	.container {
		@include px2rem(width, 556px);
		margin: 15% auto 0;
		.login-box {
			width: 100%;
			@include px2rem(height, 42px);
			border-bottom: 1px solid $border;
			display: flex;
			@include px2rem(margin-bottom, 48px);
			align-items: center;
			justify-content: space-between;
			input {
				@include px2rem(width, 450px);
				@include px2rem(height, 24px);
				@include px2rem(padding, 9px 0px 9px 24px);
				@include font-dpr(12px);
				display: block;
				border: none;
				background: none;
				color: #121212;
				outline: none;
			}
			img {
				@include px2rem(width, 26px);
				
				@include px2rem(margin-right, 32px);
			}
		}
		button {
			width: 100%;
			@include px2rem(height, 74px);
			background: #d10000;
			@include font-dpr(15px);
			@include px2rem(line-height, 74px);
			@include px2rem(margin-bottom, 48px);
			color: #eceaea;
			text-align: center;
			border: none;
			outline: none;
		}
		.other {
			display: flex;
			justify-content: space-between;
			align-items: center;
			span {
				@include font-dpr(12px);
				color: #121212;
			}
			.forget {
				text-decoration: underline;
			}
		}
	}
	.auth {
		@include px2rem(width, 360px);
		@include px2rem(height, 76px);
		margin: 0 auto;
		@include px2rem(margin-top, 140px);
		display: flex;
		align-items: center;
		justify-content: space-between;
		.line {
			width: 1px;
			@include px2rem(height,  50px);
			background: $border;
		}
		img {
			@include px2rem(width, 76px);
		}
	}
	.error {
		@include px2rem(padding, 10px 15px);
		@include px2rem(border-radius, 4px);
		background: rgba(0,0,0, .7);
		@include font-dpr(12px);
		color: #fff;
		position: absolute;
		left: 50%;
		top: 4%;
		transform: translateX(-50%);
	}
</style>


