					</swiper-slide>
					<swiper-slide>
						<div class="one-timer">
							<img src="../assets/images/food1.png" alt="">
							<p>精选海南芒果</p>
							<span>￥24.8</span>
						</div>
					</swiper-slide>
					<swiper-slide>
						<div class="one-timer">
							<img src="../assets/images/food5.png" alt="">
							<p>精选海南芒果精选海南芒果</p>
							<span>￥24.8</span>
						</div>
					</swiper-slide>
					<swiper-slide>
						<div class="one-timer">
							<img src="../assets/images/food4.png" alt="">
							<p>精选海南芒果</p>
