<script>
	export default {
		data(){
			return {
				swiperOption: {
					autoplay: 11110500,
					loop : false,
					slidesPerView: 5,
					spaceBetween: 10,
					freeMode: true,
					autoplayDisableOnInteraction : false,
				}
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import "../assets/css/_mixin.scss";
	.timer {
		background: #fff;
		@include px2rem(margin-bottom, 4px);
	}
	.timer-t {
		@include px2rem(height, 65px);
		@include px2rem(padding-left, 30px);
		display: flex;
		align-items: center;
		img {
			@include px2rem(width, 30px);
			@include px2rem(height, 30px);
			@include px2rem(margin-right, 5px);
		}
		&>span {
			@include font-dpr(14px);
			color: #d10000;
			@include px2rem(margin-right, 13px);
			line-height: 1;
		}
	}
	.count {
		@include px2rem(height, 28px);
		display: flex;
		align-items: center;
		span {
			color: #fff;
			background: #121212;
			@include px2rem(padding, 0px 6px);
			@include px2rem(height, 28px);
			@include px2rem(line-height, 28px);
			@include font-dpr(8px);
			@include px2rem(margin-right, 2px);
		}
		i {
			font-style: normal;
			@include px2rem(margin, 0px 2px);
		}
	}
	.timer-b {
		width: 100%;
		@include px2rem(height, 170px)
		display: flex;
		&>span {
			width: 4%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			img {
				@include px2rem(width, 11px);
				@include px2rem(height, 13px);
			}
		}
		.timer-b-r {
			transform: rotate(-180deg);
		}
		.timer-food {
			width: 92%;
			height: 100%;
		}
	}
	.one-timer {
		img {
			@include px2rem(width, 100px);
			@include px2rem(height, 100px);
			display: block;
			margin: 0 auto;
		}
		p {
			@include font-dpr(8px);
			@include px2rem(margin-top, 10px);
			color: #4a4949;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			text-align: center;
		}
		span {
			@include font-dpr(8px);
			color: #d10000;
			display: block;
			font-weight: bold;
			text-align: center;
		}
	}
</style>


