<template>
	<div class="home">
		<Headers></Headers>
		<Banners></Banners>
		<div class="tab">
			<ul class="tab-t">
				<li>
					<img src="../assets/images/tab1.png" alt="">
					<span>热销</span>
				</li>
				<li>
					<img src="../assets/images/tab2.png" alt="">
					<span>水产</span>
				</li>
				<li>
					<img src="../assets/images/tab3.png" alt="">
					<span>蔬菜</span>
				</li>
				<li>
					<img src="../assets/images/tab4.png" alt="">
					<span>鲜果</span>
				</li>
				<li>
					<img src="../assets/images/tab5.png" alt="">
					<span>饮品甜品</span>
				</li>
				<li>
					<img src="../assets/images/tab6.png" alt="">
					<span>菜品</span>
				</li>
			</ul>
			<div class="tab-b">
				<div class="food-title">
					<img src="../assets/images/icon3.png" alt="">
				</div>
				<div class="food-msg">
					<swiper :options="swiperOption" ref="mySwipers">
					  <!-- slides -->
					  <swiper-slide>
					  	<span>入口即化の棉花蛋糕此配方来自壹笑嫣然入口即</span>
					  </swiper-slide>
					  <swiper-slide>
					  	<span>入口即化の棉花蛋糕此配方来自壹笑嫣然入口即化の棉花蛋糕此配方来自壹笑嫣然</span>
					  </swiper-slide>
					  <swiper-slide>
					  	<span>入口即化の棉花蛋糕此配方来自壹笑嫣然入口即</span>
					  </swiper-slide>
					  <swiper-slide>
					  	<span>入口即化の棉花蛋糕此配方来自壹笑嫣然入口即</span>
					  </swiper-slide>
					  <swiper-slide>
					  	<span>入口即化の棉花蛋糕此配方来自壹笑嫣然入口即化の棉花蛋糕此配方来自壹笑嫣然</span>
					  </swiper-slide>
					</swiper> 					
				</div>
			</div>
		</div>
		<Timer></Timer>
		<Goods></Goods>
	</div>
</template>
<script>
	import Headers from "./Headers.vue"
	import Banners from "./Banners.vue"
	import Timer from "./Timer.vue"
	import Goods from "./Goods.vue"
	export default {
		data() {
		  return {
		    swiperOption: {
		      autoplay: 3500,
		      loop : false,
		      direction: "vertical",
		      autoplayDisableOnInteraction : false,
		    }
		  }
		},
		components: {
			Headers,
			Banners,
			Timer,
			Goods
		},
		mounted(){
			this.$store.dispatch("changeHead", "home");
		}
	}
</script>
<style scoped lang="scss">
	@import "../assets/css/public.scss";
	.tab {
		background: #fff;
		@include px2rem(margin-bottom, 4px);
	}
	.tab-t {
		@include px2rem(height, 137px);
		border-bottom: 1px solid $border;
		display: flex;
		align-items: center;
		li {
			flex: 1;
			display: flex;
			height: 100%;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			img {
				@include px2rem(width, 64px);
				@include px2rem(height, 60px);
				@include px2rem(margin-bottom, 14px);
			}
			span {
				@include font-dpr(12px);
				color: #4a4949;
				line-height: 1;
			}
		}
	}
	.tab-b {
		@include px2rem(height, 38px);
		display: flex;
		.food-title {
			@include px2rem(padding, 8px 30px);
			position: relative;
			img {
				@include px2rem(width, 90px);
				@include px2rem(height, 22px);
				display: block;
				margin: 0 auto;
			}
			&:after {
				content: "";
				width: 1px;
				@include px2rem(height, 36px);
				background: $border;
				position: absolute;
				right: 0;
				@include px2rem(top, 1px);
			}
		}
		.food-msg {
			flex: 1;
			@include px2rem(height, 38px);
			overflow: hidden;
			position: relative;
			.swiper-container {
				@include px2rem(height, 38px);
				.swiper-slide {
					@include px2rem(height, 38px);
				}
			}
			span {
				@include px2rem(height, 38px);
				display: block;
				@include font-dpr(10px);
				@include px2rem(line-height, 44px);
				color: #6f6f6f;
				@include px2rem(padding-left, 12px);
				overflow: hidden;
				white-space: nowrap;
				text-overflow: ellipsis;
			}
		}

	}
</style>


