<template>
  <div class="banners">
    <swiper :options="swiperOption" ref="mySwiper">
      <!-- slides -->
      <swiper-slide><img src="../assets/images/banner1.png" /></swiper-slide>
      <swiper-slide><img src="../assets/images/banner2.png" /></swiper-slide>
      <swiper-slide><img src="../assets/images/banner3.png" /></swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>   
  </div>
</template>

<script>
  export default {
    data() {
      return {
        swiperOption: {
          autoplay: 3500,
          loop : true,
          autoplayDisableOnInteraction : false,
          pagination : '.swiper-pagination',
          paginationClickable :true,
        }
      }
    }
  }
</script>
<style lang="scss">
    @import "../assets/css/_mixin.scss";
    .banners {
      width:100%;
      @include px2rem(height, 280px);
      overflow: hidden;
    }

    .swiper-pagination-bullet {
      background: #c2c2c2 !important;
      opacity: .4 !important;
    }
    .swiper-pagination-bullet-active {
      background: #fff !important;
      opacity: 1 !important;
    }
    .swiper-slide img {
      width: 100%;
      height: 100%;
    } 
</style>


