<template>
	<div>
		<div class="shop-head">
			<span class="shop-title">购物车</span>
			<span class="shop-edit">编辑</span>
		</div>
		<ol class="shop-content">
			<li v-for="(item, index) in dataJson">
				<span :class="{'check-item':true, checked:item.isChecked}" @click="selectTab(item)"></span>
				<img :src="item.img" alt="" />
				<div class="good-info">
					<p>{{item.name}}</p>
					<span>￥{{item.price}}</span>
				</div>
				<div class="good-count">
					<button @click="changeCount(item, -1, index)">-</button>
					<span>{{item.count}}</span>
					<button @click="changeCount(item, +1)">+</button>
				</div>
			</li>
		</ol>
		<div class="total-container">
			<div class="total-check" @click="unSelectAll">
				<span :class="{checked:isSelectAll}"></span>
				<i>全选</i>
			</div>
			<div class="total-price">
				<span>合计：</span>
				<i>￥{{totalPrice}}</i>
			</div>
			<div class="charge">结算</div>
		</div>
		<div class="del-container" v-show="confirmDelete">
			<div class="container-box">
				<h5>确定要删除此条商品吗？</h5>
				<div class="btn-list">
					<button @click="delGood(true)">删除</button>
					<button @click="delGood(false)">取消</button>
				</div>				
			</div>
		</div>
	</div>
</template>
<script>
	import imgUrl from "../assets/images/shop1.png"
	export default {
		data(){
			return {
				dataJson: [
					{
						name: "光明 赏味酪乳风味发酵乳 原 味无添加酸奶 135g*3",
						price: "2.5",
						img: imgUrl,
						count: 1
					},
					{
						name: "光明 赏味酪乳风味发酵乳 原 味无添加酸奶 135g*3",
						price: "3.0",
						img: imgUrl,
						count: 1
					},
					{
						name: "光明 赏味酪乳风味发酵乳 原 味无添加酸奶 135g*3",
						price: "4.0",
						img: imgUrl,
						count: 1
					}
				],
				isSelectAll:false,
				confirmDelete:false,
				readyToDelIndex:-1,
			}
		},
		methods: {
			selectTab(good){
				if( !good.isChecked ){
					this.$set(good,"isChecked",true)
				}else{
					good.isChecked = !good.isChecked;
				}
				this.selectAll();
			},
			selectAll(){
				var flag = true;
				this.dataJson.forEach(function(good){
					if( !good.isChecked ){
						flag = false;
						return;
					}
				})
				this.isSelectAll = flag;
			},
			unSelectAll(){
				this.isSelectAll = !this.isSelectAll;
				this.dataJson.forEach( good => good.isChecked=this.isSelectAll );
			},
			changeCount(good, val, index){
				if( good.count==1 && val==-1){
					this.confirmDelete = true;
					this.readyToDelIndex = index;
				}else{
					good.count += val;
				}
			},
			delGood(state){
				if( !state ){
					this.confirmDelete = false;
					this.readyToDelIndex = -1;
				}else{
					this.dataJson.splice(this.readyToDelIndex, 1);
					this.confirmDelete = false;
					this.readyToDelIndex = -1;
				}
			}

		},
		computed: {
			totalPrice(){
				var tprice = 0;
				this.dataJson.forEach(function(list){
					if( list.isChecked ){
						tprice += (list.price-0)*list.count;
					}
				})
				return parseFloat( tprice.toFixed(2) );
			}
		}
	}
</script>
<style scoped lang="scss">
	@import "../assets/css/public.scss";
	.shop-head {
		@include px2rem(padding, 12px 45px);
		@include px2rem(height, 62px);
		background: #fff;
		@include px2rem(margin-bottom, 4px);
		position: relative;
		text-align: center;
		.shop-title {
			@include font-dpr(17px);
			color: #121212;
			@include px2rem(line-height, 62px);
		}
		.shop-edit {
			@include font-dpr(14px);
			@include px2rem(line-height, 86px);
			@include px2rem(right, 45px);
			color: #121212;
			position: absolute;
			top: 0;
		}
	}
	.shop-content {
		@include px2rem(padding, 0px 30px);
		background: #fff;
		li {
			width: 100%;
			@include px2rem(height, 134px);
			@include px2rem(padding, 30px 0px);
			display: flex;
			align-items: center;
			border-bottom: 1px solid $border;
			.check-item {
				@include px2rem(width, 32px);
				@include px2rem(height, 32px);
				background: url("../assets/images/shop2.png") no-repeat;
				background-size: 100% 100%;
			}
			.check-item.checked {
				background: url("../assets/images/shop3.png") no-repeat;
				background-size: 100% 100%;				
			}
			img {
				@include px2rem(width, 126px);
				@include px2rem(height, 120px);
				@include px2rem(padding, 2px);
				@include px2rem(margin, 0px 12px);
				border: 1px solid $border;
			}
			.good-info {
				flex: 1;
				p {
					@include font-dpr(12px);
					@include px2rem(line-height, 36px);
					color: #4a4949;
				}
				span {
					@include font-dpr(12px);
					@include px2rem(line-height, 40px);
					color: #d10000;
				}
			}
			.good-count {
				@include px2rem(width, 112px);
				align-self: flex-end;
				@include font-dpr(12px);
				color: #4a4949;
				display: flex;
				align-items: center;
				button {
					@include px2rem(width, 30px);
					@include px2rem(width, 30px);
					text-align: center;
					@include px2rem(line-height, 30px);
					@include font-dpr(16px);
					border: 1px solid $border;
					background: none;
				}
				span {
					@include px2rem(width, 44px);
					text-align: center;
				}
			}
		}
	}
	.total-container {
		@include px2rem(height, 86px);
		width: 100%;
		background: #fff;
		display: flex;
		align-items: center;
		position: fixed;
		left: 0;
		@include px2rem(bottom, 98px);
		border-bottom: 1px solid $border;
		z-index: 2;
		.total-check {
			display: flex;
			justify-content: center;
			align-items: center;
			@include px2rem(padding, 0px 10px 0px 40px);
			span {
				@include px2rem(width, 32px);
				@include px2rem(height, 32px);
				background: url("../assets/images/shop2.png") no-repeat;
				background-size: 100% 100%;				
			}
			span.checked {
				background: url("../assets/images/shop3.png") no-repeat;
				background-size: 100% 100%;					
			}
			i {
				font-style: normal;
				color: #6f6f6f;
				@include font-dpr(14px);
				@include px2rem(margin-left, 15px);
			}
		}
		.total-price {
			flex: 1;
			@include font-dpr(14px);
			span {
				@include px2rem(maring, 0px 12px 0px 24px);
				color: #121212;
			}
			i {
				font-style: normal;
				color: #d10000;
			}
		}
		.charge {
			@include px2rem(width, 212px);
			@include px2rem(line-height, 86px);
			text-align: center;
			@include font-dpr(14px);
			color: #fff;
			background: #d10000;
		}
	}
	.del-container {
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 100;
		background: rgba(0,0,0, .5);
		.container-box {
			@include px2rem(width, 620px);
			@include px2rem(height, 320px);
			@include px2rem(border-radius, 4px);
			background: #fff;
			position: absolute;
			left: 50%;
			top: 50%;
			@include px2rem(margin-left, -310px);
			@include px2rem(margin-top, -160px);
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: space-between;
			h5 {
				@include font-dpr(14px);
				color: #121212;
				@include px2rem(margin-top, 100px);
			}
			.btn-list {
				width: 100%;
				@include px2rem(height, 80px);
				display: flex;
				button {
					flex: 1;
					height: 100%;
					border: none;
					@include font-dpr(14px);
				}
				button:first-child {
					background: #d10000;
					color: #fff;
				}
			}
		} 


	}
</style>


