import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Discover from '@/components/Discover'
import Classify from '@/components/Classify'
import Shopcar from '@/components/Shopcar'
import Person from '@/components/Person'
import Community from '@/components/Community'
import Subscribe from '@/components/Subscribe'
import Chose from '@/components/Chose'
import Login from '@/components/Login'

Vue.use(Router)

export default new Router({
    //mode: "history",
    routes: [
        {
        	path: '/',
        	name: 'home',
        	component: Home
        },
        {
        	path: '/discover',
        	name: 'discover',
        	component: Discover,
            children: [
                {
                    path: 'chose',
                    name: 'chose',
                    component: Chose
                },
                {
                    path: 'subscribe',
                    name: 'subscribe',
                    component: Subscribe
                },
                {
                    path: 'community',
                    name: 'community',
                    component: Community                   
                }
            ]
        },
        {
        	path: '/classify',
        	name: 'classify',
        	component: Classify
        },
        {
        	path: '/shopcar',
        	name: 'shopcar',
        	component: Shopcar
        },
        {
            path: '/person',
            name: 'person',
            component: Person
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        }
    ]
})



