<template>
	<div>
		<Headers></Headers>
		<ul class="tab">
			<li @touchstart="toPage('/discover/chose')" :class="{'router-link-exact-active': $route.path=='/discover/chose'}">
			  <span>精选</span>
			</li> 
			<li @touchstart="toPage('/discover/subscribe')" :class="{'router-link-exact-active': $route.path=='/discover/subscribe'}">
			  <span>订阅</span>
			</li> 
			<li @touchstart="toPage('/discover/community')" :class="{'router-link-exact-active': $route.path=='/discover/community'}">
			  <span>社区</span>
			</li> 
		</ul>
		<router-view></router-view>
	</div>
</template>
<script>
	import Headers from "./Headers.vue"
	export default {
		methods: {
			toPage(str){
				this.$router.push(str);
			}
		},
		components: {
			Headers
		},
		mounted(){
			this.$store.dispatch("changeHead", "dis");
		}
	}
</script>
<style scoped lang="scss">
	@import "../assets/css/public.scss";
	.tab {
		@include px2rem(height, 90px);
		@include px2rem(padding, 0px 70px 4px);
		display: flex;
		align-items: center;
		background: #fff;
		border-top: 1px solid $border;
		li {
			flex: 1;
			height: 100%;
			text-align: center;
			span {
				@include font-dpr(14px);
				@include px2rem(line-height, 84px);
				color: #4a4949;
				position: relative;
			}

		}

	}
	.router-link-exact-active span {
		color: #d10000 !important;
		&:after {
			content: "";
			@include px2rem(width, 34px);
			@include px2rem(height, 3px);
			background: #d10000;
			position: absolute;
			@include px2rem(top, 40px);
			left: 50%;
			transform: translate(-50%);
		}
	}
</style>


