<template>
	<div class="headers">
		<div class="h-l" v-html="headState"></div>
		<div class="h-m" @click="inputFocus">
			<button></button>
			<input type="text" placeholder="青口贝菜谱 大闸蟹菜谱" :value="values" ref="inputs">
		</div>
		<div class="h-r">
			<div>
				<span>+16</span>
			</div>
		</div>
	</div>
</template>
<script>
	import {mapGetters} from "vuex"
	export default {
		data(){
			return {
				values: "",
			}
		},
		computed: mapGetters([
			"headState"
		]),
		methods: {
			inputFocus(){
				this.$refs.inputs.focus();
			}
		}
	}
</script>
<style lang="scss">
	@import "../assets/css/_mixin.scss";
	.headers {
		@include px2rem(padding, 12px 25px);
		@include px2rem(height, 62px);
		background: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		.h-l {
			@include px2rem(width, 104px);
			display: flex;
			justify-content: center;
			align-items: center;
			&>span {
				@include font-dpr(12px);
				@include px2rem(line-height, 62px);
				color: $fontColor;				
			}
			&>img {
				@include px2rem(width, 40px);
				@include px2rem(width, 40px);
			}
		}
		.h-m {
			flex: 1;
			height: 100%;
			@include px2rem(border-radius, 62px);
			background: #f2f2f0;
			display: flex;
			align-items: center;
			justify-content: center;
			button {
				border: none;
				@include px2rem(width, 30px);
				@include px2rem(height, 28px);
				@include px2rem(margin-right, 9px);
				background: url("../assets/images/icon2.png") no-repeat;
				background-size: 100% 100%;
			}
			input {
				display: block;
				width: 58%;
				@include font-dpr(12px);
				line-height: 1;
				color: $fontColor;
				background: none;
				border: none; 
			}
		}
		.h-r {
			@include px2rem(width, 104px);
			display: flex;
			justify-content: center;
			align-items: center;
			div {
				@include px2rem(width, 41px); 
				@include px2rem(height, 40px); 
				background: url("../assets/images/icon1.png") no-repeat;
				background-size: 100% 100%;
				position: relative;
				span {
					background: #d10000;
					@include font-dpr(10px);
					position: absolute;
					@include px2rem(left, 18px);
					@include px2rem(top, -15px);
					@include px2rem(padding, 4px 8px);
					@include px2rem(border-radius, 30px);
					color: #fff
				}
			}
		}
	}
</style>


