<template>
	<div class="classify">
		<Headers></Headers>	
		<div class="content" ref="content" id="content">
			<ol class="content-l">
				<li v-for="(v, k) in subTab" @click="toGet(v.tab, k)" :class="{active: k==index}">{{v.sub}}</li>	
			</ol>
			<div class="content-r">
				<div class="sub-img">
					<img src="../assets/images/class1.png" alt=""/>
				</div>
				<div class="content-i">
					<div class="title">
						<img src="../assets/images/class8.png" alt=""/>
						<span>分类</span>
					</div>
					<ul class="info">
						<li v-for="item in subInfo4">
							<img :src="item.img" alt="">
							<span>{{item.sub}}</span>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import Headers from "./Headers.vue"
	import p1 from "../assets/images/class2.png"
	import p2 from "../assets/images/class3.png"
	import p3 from "../assets/images/class4.png"
	import p4 from "../assets/images/class5.png"
	import p5 from "../assets/images/class6.png"
	import p6 from "../assets/images/class7.png"
	export default {
		data(){
			return {
				index: 2,
				subTab: [
					{sub: "推荐", tab: 1},
					{sub: "试吃", tab: 2},
					{sub: "海鲜水产", tab: 3},
					{sub: "新鲜果蔬", tab: 1},
					{sub: "猪肉牛肉", tab: 2},
					{sub: "禽类蛋类", tab: 3},
					{sub: "饮品甜品", tab: 1},
					{sub: "其他水产", tab: 2},
					{sub: "推荐2", tab: 3},
					{sub: "推荐3", tab: 1},
					{sub: "推荐4", tab: 2},
					{sub: "推荐5", tab: 3},
					{sub: "推荐6", tab: 1},
					{sub: "推荐7", tab: 2},
				],
				subInfo1: [
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p1, sub: "虾类"},
					{img: p2, sub: "鱼类"},
					{img: p3, sub: "蟹类"},
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p6, sub: "其他水产"},
				],
				subInfo2: [
					{img: p3, sub: "蟹类"},
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p6, sub: "其他水产"},
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p1, sub: "虾类"},
					{img: p2, sub: "鱼类"},
					{img: p3, sub: "蟹类"},
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p6, sub: "其他水产"},
					{img: p4, sub: "贝类"},
					{img: p3, sub: "蟹类"},
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p6, sub: "其他水产"},
				],
				subInfo3: [
					{img: p1, sub: "虾类"},
					{img: p2, sub: "鱼类"},
					{img: p3, sub: "蟹类"},
					{img: p4, sub: "贝类"},
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p1, sub: "虾类"},
					{img: p2, sub: "鱼类"},
					{img: p3, sub: "蟹类"},
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p6, sub: "其他水产"},
				],
				subInfo4: [
					{img: p1, sub: "虾类"},
					{img: p2, sub: "鱼类"},
					{img: p3, sub: "蟹类"},
					{img: p4, sub: "贝类"},
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p1, sub: "虾类"},
					{img: p2, sub: "鱼类"},
					{img: p3, sub: "蟹类"},
					{img: p4, sub: "贝类"},
					{img: p5, sub: "海参"},
					{img: p6, sub: "其他水产"},
				]
			}
		},
		components: {
			Headers
		},
		methods: {
			toGet(id, k){
				this.index = k;
				var str= "subInfo"+id;
				this.subInfo4 = this[str];
			}
		},
		mounted(){
			this.$store.dispatch("changeHead", "home");
			var marginTop = this.$refs.content.offsetTop;
			var bottomHeight = document.getElementsByTagName('footer')[0].offsetHeight;
			var bodyHeight = document.documentElement.clientHeight;  
			var setHeight = bodyHeight-bottomHeight-marginTop+"px";
			document.getElementById("content").style= 'height:'+setHeight+';overflow:hidden;';
			document.getElementById("content").scrollTop = 0;
		},
		
	}
</script>
<style lang="scss" scoped>
	@import "../assets/css/_mixin.scss";
	.content {
		@include px2rem(margin-top, 4px);
		@include px2rem(min-height, 460px);
		.content-l {
			width: 20%;
			height: 100%;
			float: left;
			overflow: hidden;
			overflow-y: auto;
			background: #fff;
			li {
				float: left;
				width: 100%;
				@include px2rem(height, 90px);
				@include font-dpr(11px);
				color: #4a4949;
				@include px2rem(line-height, 90px);
				text-align: center;
			}
			li.active {
				@include font-dpr(13px);
				color: #d10000;
			}
		}
		.content-r {
			width: 79.5%;
			height: 100%;
			background: #fff;
			overflow: hidden;
			overflow-y: auto;
			float: right;
		}
		.sub-img {
			@include px2rem(padding-top, 22px);
			@include px2rem(width, 584px);
			margin: 0 auto;
			img {
				@include px2rem(width, 584px);
				@include px2rem(height, 164px);
			}
		}
		@at-root .content-i {
			width: 100%;
			.title {
				display: flex;
				justify-content: center;
				align-items: center;
				@include px2rem(padding, 26px 0px 20px);
				img {
					@include px2rem(width, 24px);
					@include px2rem(width, 20px);
				}
				span {
					@include font-dpr(13px);
					color: #4a4949;
					line-height: 1;
					@include px2rem(margin-left, 2px);
				}
			}
			.info {
				@include px2rem(padding, 0px 45px);
				display: flex;
				flex-wrap: wrap;
				li {
					@include px2rem(width, 146px);
					@include px2rem(height, 192px);
					@include px2rem(margin, 10px);
					background: #f8f8f8;
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: center;
					img {
						@include px2rem(width, 110px);
						@include px2rem(height, 110px);
						@include px2rem(padding-top, 30px);
						@include px2rem(margin-bottom, 14px);
					}
					span {
						@include font-dpr(11px);
						color: #4a4949;
					}
				}
			}
		}
		
	}

</style>


