import * as types from "./types.js"
import getters from "./getters.js"
import initImgUrl from "../assets/images/add.png"

const state = {
	headState: "菜谱"
};

const mutations = {
	[types.CHANGEHEAD](state, tag){
		if( tag == "home" ){
			state.headState = '<img src="'+initImgUrl+'" class="add" />';
		}else if( tag == "dis"){
			state.headState = '<span>菜谱</span>';
		}
	}
}

export default {
	state,
	mutations,
	getters
}


