<template>
  <div id="app">
    <router-view></router-view>
    <div class="empty"></div>
    <footer>
      <div @touchstart="toPage('/')" :class="{'router-link-exact-active': $route.path=='/'}">
        <i class="icons home"></i>
        <span>首页</span>
      </div> 
      <div @touchstart="toPage('/classify')" :class="{'router-link-exact-active': $route.path=='/classify'}">
        <i class="icons classify"></i>
        <span>分类</span>
      </div> 
      <div @touchstart="toPage('/discover/chose')" :class="{'router-link-exact-active': ($route.path).indexOf('/discover')>-1}">
        <i class="icons discover"></i>
        <span>发现</span>
      </div> 
      <div @touchstart="toPage('/shopcar')" :class="{'router-link-exact-active': $route.path=='/shopcar'}">
        <i class="icons shopcar"></i>
        <span>购物车</span>
      </div> 
      <div @touchstart="toPage('/login')" :class="{'router-link-exact-active': $route.path=='/login' || $route.path=='/person'}">
        <i class="icons person"></i>
        <span>我的</span>
      </div> 
    </footer>
  </div>
</template>

<script>
export default {
  methods:{
    toPage(str){
      if( str === "/login"){
        if( localStorage.getItem("ywc_app") ==="youwenchao"){
          this.$router.push("/person");
        }else{
          this.$router.push("/login");
        }
      }else{
        this.$router.push(str);
      }
      
    },
    scrollTop(){
      document.body.scrollTop = 0;
    }
  },
  created(){

  },
  watch: {
    "$route": "scrollTop"
  }
}
</script>

<style lang="scss">
  @import "./assets/css/_mixin.scss";
  html, body {
    width: 100%;
    min-height: 100%;
    background-color: #ebebeb;
  }
  footer {
    background: #fff;
    position: fixed;
    left: 0;
    bottom: 0;
    @include px2rem(height, 98px);
    width: 100%;
    display: flex;
    align-items: center;
    box-shadow: 0 0 8px 4px rgba(240,240,240, .7);
    z-index: 1;
    div {
      height: 100%;
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .icons {
        display: block;
        @include px2rem(width, 50px);
        @include px2rem(height, 44px);
        @include px2rem(margin-top, 12px);
        @include px2rem(margin-bottom, 4px);
      }
      .home {
        background: url("./assets/images/f1.png");
        background-size: 100% 100%;
      }
      .classify {
        background: url("./assets/images/f3.png");
        background-size: 100% 100%;
      }
      .discover {
        background: url("./assets/images/f5.png");
        background-size: 100% 100%;
      }
      .shopcar {
        background: url("./assets/images/f7.png");
        background-size: 100% 100%;
      }
      .person {
        background: url("./assets/images/f9.png");
        background-size: 100% 100%;
      }
      span {
        @include font-dpr(10px);
        line-height: 1.1;
        color: #6f6f6f;
      }
    }
    .router-link-exact-active {
      .home {
        background: url("./assets/images/f2.png");
        background-size: 100% 100%;
      }
      .classify {
        background: url("./assets/images/f4.png");
        background-size: 100% 100%;
      }
      .discover {
        background: url("./assets/images/f6.png");
        background-size: 100% 100%;
      }
      .shopcar {
        background: url("./assets/images/f8.png");
        background-size: 100% 100%;
      }
      .person {
        background: url("./assets/images/f10.png");
        background-size: 100% 100%;
      }
      span {
        color: $active;
      }
    }

  }
  .empty {
    width: 100%;
    @include px2rem(height, 98px);
  }
</style>



